#keyboardPanel {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
#keyboardPanel .row {
  display: flex;
  gap: 5px;
  height: 16.6666666667%;
}
#keyboardPanel .row div {
  cursor: pointer;
  padding: 5px;
  border-radius: 2px;
  border: 2px solid var(--txt);
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#keyboardPanel .row div.pressed {
  background: var(--txt);
  color: var(--back);
}